.card {
    background: #fff;
    border-radius: 2px;
    height: 160px;
    overflow: hidden;
    margin: 16px;
    position: relative;
    width: 250px;
    -webkit-box-shadow: 0 3px 6px rgb(170, 207, 216), 0 3px 6px #999;
            box-shadow: 0 3px 6px rgb(170, 207, 216), 0 3px 6px #999;
    text-align: left;
}

.card > .img-container {
    width: 100%;
    height: auto;
    overflow: hidden;
    text-align: center;
}
.card > .img-container img {
    margin-top: 10px;
    width: 100%;
    height: 140px;
}

.style_prevu_kit
{
    display:inline-block;
    border:0;
    width:200px;
    height:220px;
    position: relative;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1); 
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1); 
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1);
    -o-transition: all 200ms ease-in;
    transition: all 200ms ease-in;
    transform: scale(1);   
}

.style_prevu_kit:hover
{
    cursor:pointer;
    -webkit-box-shadow: 0px 0px 150px #000000;
            box-shadow: 0px 0px 150px #000000;
    z-index: 2;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1.3);
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1.3);   
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1.3);
    -o-transition: all 200ms ease-in;
    transition: all 200ms ease-in;
    transform: scale(1.3);
}










